.neuro {
  color: $neuro-text;
  background: $neuro;
  box-shadow: 3px 3px 7px rgba(174, 174, 192, 0.4), -3px -3px 7px $white;
  transition: box-shadow 0.3s ease-out;

  &--accent {
    color: $neuro-accent;
  }

  &:hover,
  &:focus {
    box-shadow: 6px 6px 15px rgba(174, 174, 192, 0.8), -6px -6px 15px $white;
  }

  &--inset {
    border: 1px solid darken($color: $neuro, $amount: 15) !important;
    box-shadow: inset 3px 3px 7px rgba(174, 174, 192, 0.2), inset -3px -3px 3px rgba(174, 174, 192, 0.2);

    &:hover,
    &:focus {
      box-shadow: inset 6px 6px 10px rgba(174, 174, 192, 0.3), inset -6px -6px 5px rgba(174, 174, 192, 0.3);
    }
  }

  &--link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 35px;
    border-radius: 4px;
  }
}
